.container{
    margin-bottom:350px;
    max-width: 100%;
    display: flex;
     //background-color: #b0d9af;
    //background-image: linear-gradient(0deg, #D9AFD9 0%, #97D9E1 100%);
    flex-direction: column;
    img{
        height: 25px;
        width: 25px;
        padding: -50px;
        margin-left: 25px;
        margin-top:25px;
    }
    .text{
        text-align: center;
        font-family: 'Black Ops One', cursive;
     background: -webkit-linear-gradient(90deg, rgb(89, 201, 95) 33%, rgba(9,121,71,1) 70%, rgb(4, 47, 56) 100%);;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        padding: 25px;
        font-size: 25px;
    
    }

   
}

@media screen and (min-width:1024px){

.container{
    background-repeat: no-repeat;
    background-size: cover;
    height: 500px;

    img{
        width: 50px;
        height: 50px;
    }
    .text{
        font-size: 45px;
        text-align: center;
    }

}
    

}